import { useState, useEffect } from "react";
import {
  Search,
  Plus,
  Play,
  Square,
  RefreshCw,
  Trash2,
  Settings,
  ExternalLink,
  Terminal,
  CheckCircle,
  XCircle,
  Clock,
  GitBranch,
  Code2,
  Activity,
  Zap,
  Shield,
} from "lucide-react";
import { apiUrl } from "@/utils/api";

interface Project {
  name: string;
  repoUrl: string;
  branch: string;
  path: string;
  dockerfile: string;
  composeFile: string;
  environmentVars: Record<string, string>;
  containers: string[];
  status: "configured" | "building" | "running" | "stopped" | "failed";
  createdAt: string;
  lastUpdated: string;
  buildHistory: Array<{
    timestamp: string;
    status: string;
    duration: number;
  }>;
  deployHistory: Array<{
    timestamp: string;
    status: string;
    containers: number;
  }>;
  healthChecks: Array<{
    name: string;
    status: "healthy" | "unhealthy" | "unknown";
    lastCheck: string;
  }>;
  autoRestart: boolean;
  resourceLimits: {
    memory: string;
    cpu: string;
  };
}

export default function Projects() {
  const [projects, setProjects] = useState<Project[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState("");
  const [showAddModal, setShowAddModal] = useState(false);
  const [newProject, setNewProject] = useState({
    name: "",
    repository: "",
    branch: "main",
  });

  // Fetch projects from backend
  const fetchProjects = async () => {
    try {
      setLoading(true);
      const response = await fetch(apiUrl("/api/projects"));
      if (!response.ok) {
        throw new Error("Failed to fetch projects");
      }
      const result = await response.json();
      setProjects(result.data || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to fetch projects");
    } finally {
      setLoading(false);
    }
  };

  // Add new project
  const handleAddProject = async () => {
    if (!newProject.name || !newProject.repository) {
      return;
    }

    try {
      const response = await fetch(apiUrl("/api/projects"), {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name: newProject.name,
          repoUrl: newProject.repository,
          branch: newProject.branch || "main",
        }),
      });

      if (!response.ok) {
        throw new Error("Failed to add project");
      }

      await fetchProjects();
      setNewProject({ name: "", repository: "", branch: "main" });
      setShowAddModal(false);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to add project");
    }
  };

  // Build project
  const handleBuildProject = async (projectName: string) => {
    try {
      const response = await fetch(
        apiUrl(`/api/projects/${projectName}/build`),
        {
        method: "POST",
        },
      );

      if (!response.ok) {
        throw new Error("Failed to build project");
      }

      await fetchProjects();
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to build project");
    }
  };

  // Deploy project
  const handleDeployProject = async (projectName: string) => {
    try {
      const response = await fetch(
        apiUrl(`/api/projects/${projectName}/deploy`),
        {
        method: "POST",
        },
      );

      if (!response.ok) {
        throw new Error("Failed to deploy project");
      }

      await fetchProjects();
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to deploy project");
    }
  };

  // Stop project
  const handleStopProject = async (projectName: string) => {
    try {
      const response = await fetch(
        apiUrl(`/api/projects/${projectName}/stop`),
        {
        method: "POST",
        },
      );

      if (!response.ok) {
        throw new Error("Failed to stop project");
      }

      await fetchProjects();
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to stop project");
    }
  };

  // Delete project
  const handleDeleteProject = async (projectName: string) => {
    if (!confirm(`Are you sure you want to delete project "${projectName}"?`)) {
      return;
    }

    try {
      const response = await fetch(apiUrl(`/api/projects/${projectName}`), {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error("Failed to delete project");
      }

      await fetchProjects();
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to delete project");
    }
  };

  useEffect(() => {
    fetchProjects();
  }, []);

  const getStatusIcon = (status: Project["status"]) => {
    switch (status) {
      case "configured":
        return <CheckCircle className="w-4 h-4 text-blue-600" />;
      case "building":
        return <Clock className="w-4 h-4 text-yellow-600 animate-pulse" />;
      case "running":
        return <CheckCircle className="w-4 h-4 text-green-600" />;
      case "stopped":
        return <Square className="w-4 h-4 text-gray-600" />;
      case "failed":
        return <XCircle className="w-4 h-4 text-red-600" />;
      default:
        return null;
    }
  };

  const getStatusColor = (status: Project["status"]) => {
    switch (status) {
      case "configured":
        return "text-blue-600";
      case "building":
        return "text-yellow-600";
      case "running":
        return "text-green-600";
      case "stopped":
        return "text-gray-600";
      case "failed":
        return "text-red-600";
      default:
        return "text-gray-600";
    }
  };

  const filteredProjects = projects.filter(project =>
    project.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    project.repoUrl.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <h2 className="text-xl font-light text-gray-900 mb-2">Loading Projects...</h2>
          <p className="text-gray-500">Fetching project data</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <XCircle className="w-8 h-8 text-red-600" />
          </div>
          <h2 className="text-xl font-light text-gray-900 mb-2">Error Loading Projects</h2>
          <p className="text-gray-500 mb-4">{error}</p>
          <button
            onClick={fetchProjects}
            className="px-4 py-2 bg-gray-900 hover:bg-gray-800 text-white rounded-lg transition-colors duration-200"
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-6 py-8">
        <div className="flex items-center justify-between mb-8">
          <h1 className="text-3xl font-light text-gray-900 tracking-tight">Projects</h1>
          <div className="flex items-center space-x-4">
            <div className="relative">
              <Search className="w-5 h-5 text-gray-400 absolute left-3 top-1/2 transform -translate-y-1/2" />
              <input
                type="text"
                placeholder="Search projects..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            <button
              onClick={() => setShowAddModal(true)}
              className="flex items-center px-4 py-2 bg-gray-900 hover:bg-gray-800 text-white rounded-lg transition-colors duration-200"
            >
              <Plus className="w-4 h-4 mr-2" />
              Add Project
            </button>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredProjects.map((project, index) => (
            <div key={index} className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 hover:shadow-md transition-shadow duration-200">
              <div className="flex items-center justify-between mb-4">
                <div className="flex items-center">
                  {getStatusIcon(project.status)}
                  <h3 className="ml-2 text-lg font-semibold text-gray-900">{project.name}</h3>
                </div>
                <div className="flex items-center space-x-2">
                  <button
                    onClick={() => handleBuildProject(project.name)}
                    className="p-1.5 text-blue-600 hover:bg-blue-50 rounded transition-colors duration-200"
                    title="Build"
                  >
                    <Code2 className="w-4 h-4" />
                  </button>
                  <button
                    onClick={() => handleDeployProject(project.name)}
                    className="p-1.5 text-green-600 hover:bg-green-50 rounded transition-colors duration-200"
                    title="Deploy"
                  >
                    <Play className="w-4 h-4" />
                  </button>
                  <button
                    onClick={() => handleStopProject(project.name)}
                    className="p-1.5 text-gray-600 hover:bg-gray-50 rounded transition-colors duration-200"
                    title="Stop"
                  >
                    <Square className="w-4 h-4" />
                  </button>
                  <button
                    onClick={() => handleDeleteProject(project.name)}
                    className="p-1.5 text-red-600 hover:bg-red-50 rounded transition-colors duration-200"
                    title="Delete"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                </div>
              </div>

              <div className="space-y-4">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Repository:</span>
                  <a
                    href={project.repoUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-600 hover:text-blue-800 flex items-center"
                  >
                    <ExternalLink className="w-3 h-3 mr-1" />
                    {project.repoUrl.split('/').pop()}
                  </a>
                </div>

                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Branch:</span>
                  <span className="font-medium flex items-center">
                    <GitBranch className="w-3 h-3 mr-1" />
                    {project.branch}
                  </span>
                </div>

                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Status:</span>
                  <span className={`font-medium ${getStatusColor(project.status)}`}>
                    {project.status}
                  </span>
                </div>

                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Containers:</span>
                  <span className="font-medium">{project.containers.length}</span>
                </div>

                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Resources:</span>
                  <span className="font-medium text-xs">
                    {project.resourceLimits.memory} / {project.resourceLimits.cpu} CPU
                  </span>
                </div>

                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Created:</span>
                  <span className="font-medium">
                    {new Date(project.createdAt).toLocaleDateString()}
                  </span>
                </div>

                {project.healthChecks.length > 0 && (
                  <div className="pt-4 border-t border-gray-200">
                    <div className="text-sm text-gray-500 mb-2">Health Checks:</div>
                    <div className="space-y-1">
                      {project.healthChecks.slice(0, 2).map((check, idx) => (
                        <div key={idx} className="flex items-center justify-between text-xs">
                          <span className="text-gray-600">{check.name}</span>
                          <span className={`${
                            check.status === 'healthy' ? 'text-green-600' : 
                            check.status === 'unhealthy' ? 'text-red-600' : 
                            'text-gray-500'
                          }`}>
                            {check.status}
                          </span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>

        {filteredProjects.length === 0 && (
          <div className="text-center py-12">
            <Code2 className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">No projects found</h3>
            <p className="text-gray-500">Get started by adding your first project</p>
          </div>
        )}
      </div>

      {/* Add Project Modal */}
      {showAddModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-2xl border border-gray-200 max-w-md w-full m-4">
            <div className="p-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Add New Project</h3>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Project Name
                  </label>
                  <input
                    type="text"
                    value={newProject.name}
                    onChange={(e) => setNewProject({ ...newProject, name: e.target.value })}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="Enter project name"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Repository URL
                  </label>
                  <input
                    type="text"
                    value={newProject.repository}
                    onChange={(e) => setNewProject({ ...newProject, repository: e.target.value })}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="https://github.com/user/repo"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Branch
                  </label>
                  <input
                    type="text"
                    value={newProject.branch}
                    onChange={(e) => setNewProject({ ...newProject, branch: e.target.value })}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="main"
                  />
                </div>
              </div>
              <div className="flex justify-end space-x-3 mt-6">
                <button
                  onClick={() => setShowAddModal(false)}
                  className="px-4 py-2 text-gray-700 bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors duration-200"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAddProject}
                  className="px-4 py-2 bg-gray-900 hover:bg-gray-800 text-white rounded-lg font-light transition-colors duration-200"
                >
                  Add Project
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
